import { HrButton, HrColor, HrStyle, HrWeight } from './components/hrButton';
import isSelectionInHr from './utils/isSelectionInHr';

export default function toolbar(hrConfig) {
  const hrLocaleConfig = hrConfig.locale || {};
  return {
    buttons: {
      default: HrButton,
      color: HrColor,
      style: HrStyle,
      weight: HrWeight,
    },
    locale: hrLocaleConfig,
    getTarget(controller) {
      if (isSelectionInHr(controller)) {
        return 'hr';
      }
      return undefined;
    },
  };
}
